@import "C:/FrontStudy/nomadgram/frontend/src/config/_variables.scss";
.loading {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-content: center;
  margin-left: 200px;
  margin-top: 100px;
}

.formComponent {
  display: flex;
  flex-direction: column;
  width: 500px;
  height: 100%;
  margin-left: 90px;
  margin-top: 30px;
  @include breakpoint("phone-tablet") {
    margin: 15px;
  }
  box-sizing: content-box;
  .imageField {
    width: 100%;
    display: flex;
    margin: 10px auto;
    margin-bottom: 40px;
    .image {
      width: 30%;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      float: right;
      @include breakpoint("phone-tablet") {
        width: 10%;
        display: flex;
        justify-content: flex-start;
        align-items: center;
      }
      .avatar {
        width: 40px;
        height: 40px;
        border-radius: 50%;
      }
    }
    .username {
      width: 70%;
      font-size: 28px;
    }
  }
    .form {
    display: flex;
    flex-direction: column;
    width: 100%;
    .field {
      width: 100%;
      display: flex;
      margin-bottom: 10px;
      @include breakpoint("phone-tablet") {
        display: flex;
        flex-direction: column;
      }
      .label {
        width: 30%;
        font-size: 18px;
        font-weight: 500;
      }
      .input {
        width: 70%;
        border: 1px solid #efefef;
        border-radius: 3px;
        background-color: $bg-color;
        margin-bottom: 5px;
        font-size: $normal-font-size;
        padding: 9px 0 7px 8px;
        font-weight: 400;
        resize: vertical; /* 상하만 가능 */
        
        &:focus {
          outline: none;
        }
        &::placeholder {
          color: $dark-grey;
          font-size: $normal-font-size;
        }
      }
    }
    .button {
      @include blueButton();
      width: 30%;
      margin-top: 10px;
    }
  } 
}


