@import "C:/FrontStudy/nomadgram/frontend/src/config/_variables.scss";
.container {
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  .box {
    @include boxBorder(no);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 500px;
    height: 200px;
    padding: 10px 16px;
    word-break: break-all;
    .content {
      width: 100%;
      .item {
        height: 50px;
        font-size: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-top: 1px solid $light-grey;
        &:first-child {
          border-bottom: 1px solid blue;
        }
        &:focus, 
        &:active {
          outline: none;
        }
        &:active {
          opacity: 0.8;
        }
        &:hover {
          background-color: $light-grey;
        }
      }
    }
  }
}

a {
  text-decoration: none;
  color: black;
}


